
import { PAGE_SIZE } from "@/shared/constants/common.const";
import Pagination from "@/shared/components/Pagination/Pagination.vue";
import VueDatePicker from '@vuepic/vue-datepicker';
import '@vuepic/vue-datepicker/dist/main.css'
import { defineComponent } from "vue";
import { CustomerDailyTransactionReport, RPT205000211_REQ_BODY, RPT205000211_RES } from "@/shared/type/RPT205000211.type";
import { ExportSheet } from "@/shared/type/ExportExcel.type";
import { ExportExcel } from "@/shared/services/ExportExcelService";

const excel = new ExportExcel()

export default defineComponent({
    name: 'RPT202001SS01',
    components: { Pagination, VueDatePicker },
    data() { 
        return {
            PAGE_SIZE,
            applicationDate: '' as unknown,
            totalItems: 0,
            currentPage: 1,
            pageNumber: 1,
            startingIndex: 1,
            isResetPage: false,
            reportList: [] as CustomerDailyTransactionReport[],
            headers: [
                {columnName: 'No'},
                {columnName: 'Reference No'},
                {columnName: 'Biz CID'},
                {columnName: 'Biz Name'},
                {columnName: 'Transaction Type'},
                {columnName: 'From Account No'},
                {columnName: 'To Account No'},
            ]
        } 
    },

    computed: {
        formattedDate(): string {
            return this.applicationDate ? this.$datetime.formatVueDatePicker(this.applicationDate as Date) : ''
        },
    },

    mounted() {
        this.requestCustomerDailyTransactionReport()
    },

    methods: {
        async requestCustomerDailyTransactionReport(mode?: 'search' | 'reset') {
            const reqBody : RPT205000211_REQ_BODY = {
                approvalRequestDate: this.applicationDate ? this.$datetime.convertDateToString(this.applicationDate as Date) : '',
                pageSize: PAGE_SIZE,
                pageNumber: mode == 'search' ? 0 : this.currentPage
            }
            
            const res = await this.$request.getRequest("RPT20500211", reqBody, false) as RPT205000211_RES;
            if (res.header.result) {
                this.reportList = res.body.list
                this.totalItems = res.body.totalCount
            }
        },
        async onClickReset() {
            this.applicationDate = '' as any
            this.isResetPage = true
			this.currentPage = 1
            this.requestCustomerDailyTransactionReport()
        },

        async exportToExcel() {
            const customHeader = [
                'Reference No',
                'Biz CID',
                'Biz Name',
                'Transaction Type',
                'From Account No',
                'To Account No',
            ]

            const reqBody : RPT205000211_REQ_BODY = {
                approvalRequestDate: this.applicationDate ? this.$datetime.convertDateToString(this.applicationDate as Date) : '',
                pageSize: this.totalItems,
                pageNumber: 0
            }
            
            const res = await this.$request.getRequest("RPT20500211", reqBody, false) as RPT205000211_RES;
            const tableForExport = this.onMappingTableData(res.body.list)

			const dataForExport: ExportSheet = {
				data: tableForExport,
				columnHeader: [customHeader],
				sheetName: 'Customer Transaction Report'
			}
			excel.exportSheet([dataForExport], 'Customer Daily Transaction Report')
        },

        onMappingTableData(lists) {
            return lists.map((item: CustomerDailyTransactionReport) => ({
                serverTransactionID: item.serverTransactionID,
                customerNo: item.customerNo,
                customerName: item.customerName,
                eBankTransactionTypeCode: `${item.eBankTransactionTypeCode} : ${this.$codeConverter.convertValue("eBankTransactionTypeCode", item.eBankTransactionTypeCode)}`,
                withdrawalAccountNo: item.withdrawalAccountNo,
                receiverAccountNo: item.receiverAccountNo
            }));
        },

        handlePagination(pageNumber: number) {
			this.isResetPage = false;
			this.currentPage = pageNumber
			this.requestCustomerDailyTransactionReport()
		},

		handleStartingIndex(startingIndex: number){
			this.startingIndex = startingIndex
		},
    }
})
